.form {
  color: #000;
  position: relative;
  width: 1000px;
  margin: 0 auto;
}

.input {
  width: 1000px;
  height: 42px;
  font-weight: 500;
  font-size: 18px;
  outline: none;
  padding-left: 15px;
  padding-right: 15px;
  border: none;
  border-radius: 20px;
  box-shadow: 10px 5px 20px 15px rgba(19, 18, 18, 0.288);
}

.button {
  position: absolute;
  top: 2px;
  right: 2px;
  width: 38px;
  height: 38px;
  outline: none;
  border: none;
  color: #fff;
  background: #249f81;
  box-shadow: 0px 0px 20px 0px rgba(19, 18, 18, 0.555);
  border-radius: 38px;
}

.button:hover {
  background: #1e2931de;
}
